// @ts-nocheck

import "./App.css";
import React, {
  useEffect,
  useState,
  useContext
} from "react";

//ui
import { Button, TextField } from "@material-ui/core";

//logic
import io from "socket.io-client";
import { DAppClient } from "@airgap/beacon-sdk";
//import _ from "underscore";
import { HexColorPicker } from "react-colorful";
import { v4 as uuidv4 } from 'uuid';
import { FirebaseContext } from "./firebaseContext";

const socketURL =
  window.location.hostname === "localhost"
    ? "ws://localhost:8000"
    : "wss://network1-backend.herokuapp.com";


const socket = io(socketURL, { transports: ["websocket"] });
const dAppClient = new DAppClient({ name: "Beacon Docs" });

const tempID = uuidv4();
function App() {

  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState('sync');
  const [showUnsync, setShowUnsync] = useState(false);
  const [color, setColor] = useState("#000000");
  const { getProfileFB, setProfileFB, getAllProfilesFB } = useContext(FirebaseContext);
  const [profile, setProfile] = useState({ color: color, timestamp: Date.now(), key: tempID, username: "Anon" });
  const [profiles, setProfiles] = useState(
    [
      { color: "blue", timestamp: Date.now(), key: tempID, username: "user x" },
    ]);
  const [usernameInput, setUsernameInput] = React.useState('Anon');

  const handleChangeUsername = (event: React.ChangeEvent<HTMLInputElement>) => {
    setUsernameInput(event.target.value);
  };

  useEffect(() => {

    async function getProfiles() {
      setProfiles(await getAllProfilesFB())
    }
    getProfiles();

  }, []);


  useEffect(() => {

    const onProfileChange = (data) => {
      console.log(data)
      console.log(data.key)
      profiles.find(function (prof, index) {
        if (prof.key === data.key) {
          console.log("found ez ! " + index)
          setProfiles([
            ...profiles.slice(0, index),
            data,
            ...profiles.slice(index + 1)
          ]
          )
          return true;
        }
        return false;
      });
    };


    socket.on('profile', onProfileChange);

    return () => {
      socket.off('profile', onProfileChange);


    };
  }, [profiles, setProfiles]);


  //fetch wallet name if it exist for example, trydrum.tez
  async function getDomain(address: string) {
    let domain;
    await fetch('https://api.tezos.domains/graphql', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        query: `
            {
              reverseRecord(address: "`+ address + `"){owner domain{name}}
            }
            `,
        variables: {
        },
      }
      ),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.data.reverseRecord) {
          domain = result.data.reverseRecord.domain.name;
          setSynced(domain);
          setProfile({ ...profile, username: domain })
        }
      });
  }

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(activeAccount.address.slice(0, 6) + "..." + activeAccount.address.slice(32, 36));
        setShowUnsync(true);
        getDomain(activeAccount.address);
        setProfile(await getProfileFB(activeAccount.address));
      }
      else {
        setSynced('sync');
        setShowUnsync(false);
      }
    }
    getAcc();

  }, [activeAccount]);



  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount())
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount())
        setSynced('sync');
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount())
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address)
      setShowUnsync(true);
      getDomain(activeAccount.address);

      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount())
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address)
        setShowUnsync(true);

        getDomain(permissions.address);

      }
      catch (error) {
        console.log("Got error:", error);
      }
    }
  }



  return (
    <div >
      <div className="top-left" style={{ fontSize: "1em", display: "flex", alignItems: "center", margin: 10 }} >
        <b>Network1</b>
      </div>

      <div style={{ display: "flex", width: "90%", marginLeft: "auto", marginRight: "auto", overflowX: "scroll" }} >

        <div style={{ textAlign: "center", margin: 6 }}>
          <div style={{ width: 100, height: 50, backgroundColor: profile.color, border: "solid 4px ", margin: 4 }}>
          </div>
          {profile.username}
        </div>

        {profiles && profiles.map((profile) => (
          (!activeAccount || profile.key !== activeAccount.address) &&
          <div key={profile.key} style={{ textAlign: "center", margin: 6 }}>
            <div style={{ width: 100, height: 50, backgroundColor: profile.color, border: "solid 4px ", margin: 4 }}>
            </div>
            {profile.username}
          </div>
        ))
        }

      </div>

      <div style={{ width: "90%", marginLeft: "auto", marginRight: "auto" }} >
        <HexColorPicker color={color} onChange={setColor} />
        <br></br>
        <TextField id="outlined-basic" label="Username" variant="outlined" placeholder="Anon" onChange={handleChangeUsername} value={usernameInput} />
        <br></br>


        <Button size={"small"} title={"update status"} onClick={() => {
          if (activeAccount) {
            setProfile({ ...profile, color: color, key: activeAccount.address, username: usernameInput })
            //add socket 
            socket.emit('profile', { ...profile, color: color, key: activeAccount.address, username: usernameInput });
            setProfileFB({ ...profile, color: color, key: activeAccount.address, username: usernameInput });
          }
          else {
            sync();
          }

        }} >  {activeAccount ? <u>update status</u> : <u>sync to join network1</u>} </Button>

      </div>

      <div className="bottom-right" style={{ position: "absolute", display: "flex", alignItems: "center" }} >
        {showUnsync && <Button size={"small"} title={"unsync"} onClick={() => { unsync() }} ><u>unsync</u> </Button>}

        {showUnsync && <div> | </div>}
        <Button title={"sync"} size={"small"} onClick={async () => { await sync(); }} ><u>{synced}</u> </Button>

      </div>
    </div>
  );
}

export default App;
